body {
  background: #09c; }

.box-megaSelect {
  max-width: 300px;
  padding: 10px;
  margin: 0 auto;
  background: #fff;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2); }
  .box-megaSelect .box-header {
    border-bottom: 1px solid #ccc;
    display: flex;
    align-items: center; }
    .box-megaSelect .box-header span {
      width: 50%;
      padding: 10px;
      text-align: center;
      text-transform: uppercase;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis; }
    .box-megaSelect .box-header input[type='text'] {
      width: 100%; }
    .box-megaSelect .box-header.v02 {
      background: #ccf2ff; }
      .box-megaSelect .box-header.v02 span {
        width: 100%; }
  .box-megaSelect .megaSelect-options {
    border-top: 2px solid #09c;
    border-bottom: 2px solid #09c; }
  .box-megaSelect .box-row {
    padding: 10px 0; }
  .box-megaSelect ul {
    max-height: 220px;
    overflow-y: auto; }
    .box-megaSelect ul::-webkit-scrollbar {
      width: 10px; }
    .box-megaSelect ul::-webkit-scrollbar-track {
      background: #e1e1e1; }
    .box-megaSelect ul::-webkit-scrollbar-thumb {
      background: #09c; }
    .box-megaSelect ul::-webkit-scrollbar-thumb:hover {
      background: #09c; }
    .box-megaSelect ul li {
      border-bottom: 1px solid #ccc;
      cursor: pointer; }
      .box-megaSelect ul li:last-child {
        border-bottom: none; }
      .box-megaSelect ul li:hover {
        background: #ccf2ff; }
  .box-megaSelect .option {
    display: flex;
    align-items: center; }
    .box-megaSelect .option span {
      padding: 5px 10px; }
      .box-megaSelect .option span.icon {
        padding: 10px;
        color: #09c;
        text-align: center; }
      .box-megaSelect .option span:first-child {
        width: 100%;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis; }
      .box-megaSelect .option span input {
        width: 100%; }

input[type='radio'] {
  cursor: pointer; }

.radio-group.group-inline .input-radio {
  display: inline-block;
  margin-right: 20px; }

.input-radio input[type="radio"] {
  display: none; }
  .input-radio input[type="radio"]:checked + label:before {
    border: 1px solid #09c;
    background: #09c;
    box-shadow: inset 0 0 0 4px #fff; }

.input-radio label {
  position: relative;
  padding-left: 25px;
  margin: 0;
  cursor: pointer;
  min-height: 1em; }
  .input-radio label:before {
    content: '';
    display: block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 1px solid #ccc;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    background: #fff; }

.box-input {
  padding: 5px 10px;
  position: relative;
  z-index: 100; }
  .box-input input {
    width: 100%;
    background: none;
    border: none;
    outline: none; }
    .box-input input + .span {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border: 1px solid #ccc;
      z-index: -100; }
    .box-input input:focus + .span {
      border: 1px solid #09c;
      box-shadow: 0 0 5px 0 rgba(0, 102, 153, 0.5); }
  .box-input.box-line input + .span {
    border: none;
    border-bottom: 2px solid #09c;
    box-shadow: none !important; }

.box-append .box-input, .box-append .icon {
  display: table-cell; }

.box-append .box-input {
  width: 100%; }

.box-append .icon {
  color: #fff;
  background: #09c;
  cursor: pointer;
  padding: 0 10px; }

.table-area {
  overflow-x: auto; }

