.card-item {
  background: #fff;
  border: none;
  box-shadow: 0 3px 8px 0 rgba(115, 143, 147, 0.4);
  padding: 20px;
  transition: box-shadow 0.3s; }
  .card-item .card-title {
    font-size: 24px; }
  .card-item .card-body {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: baseline; }
  .card-item:hover {
    box-shadow: 0 6px 16px 0 rgba(115, 143, 147, 0.5); }
  .card-item .btn {
    display: inline-block;
    min-width: 150px;
    height: auto;
    padding: 10px 20px; }
    .card-item .btn:focus, .card-item .btn:active {
      text-decoration: none; }

.logo-H {
  display: inline-block;
  vertical-align: middle;
  line-height: 1; }

.ant-menu {
  padding: 5px 0 !important;
  background: #39424e !important;
  border: none !important;
  overflow: hidden;
  position: relative;
  z-index: 999999; }
  .ant-menu .ant-menu-item {
    border-bottom: none !important;
    vertical-align: middle !important;
    line-height: 1 !important;
    padding-bottom: 0 !important;
    height: auto !important; }
    .ant-menu .ant-menu-item.ant-menu-item-active a, .ant-menu .ant-menu-item.ant-menu-item-selected a {
      color: #fff !important; }
    .ant-menu .ant-menu-item a {
      color: #b7c9cc !important;
      font-weight: bold; }

.ant-menu-submenu-title {
  color: #b7c9cc;
  font-size: 2em;
  font-weight: bold; }
  .ant-menu-submenu-title:hover {
    color: #fff !important; }

.ant-menu-submenu {
  border: none !important; }
  .ant-menu-submenu .ant-menu-sub {
    margin-top: -8px;
    background: #39424e !important;
    color: #b7c9cc;
    border-radius: 0 !important;
    padding: 0 !important; }
    .ant-menu-submenu .ant-menu-sub .ant-menu-item {
      background: none !important;
      padding-top: 10px !important;
      padding-bottom: 10px !important; }

.logo-H {
  display: inline-block;
  width: 35px; }
  .logo-H svg {
    width: 100%;
    stroke: #126e32;
    background: #39424e;
    transition: background 0.5s; }
  .logo-H path {
    fill: #f3f7f7;
    stroke-linecap: round;
    stroke-linejoin: round;
    stroke-width: 2px;
    stroke-dasharray: 259 259;
    stroke-dashoffset: -259;
    transition: stroke-dashoffset 1s, fill 0.5s 1s; }
  .logo-H:hover svg {
    background: #f3f7f7; }
    .logo-H:hover svg path {
      fill: #1ba94c;
      stroke-dashoffset: 0; }

.breadcrumb-area {
  padding: 20px;
  background: #fff; }
  .breadcrumb-area a:hover {
    color: #1ba94c !important; }

