body.is-landing-page {
  background: #74e99c !important;
  overflow: hidden;
  position: relative; }
  body.is-landing-page .landing-page-container {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column; }
  body.is-landing-page .avatar-area {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center; }
  body.is-landing-page .avatar {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    border: 5px solid #39424e;
    background: #fff;
    padding: 35px; }
    body.is-landing-page .avatar path {
      fill: #39424e; }
    body.is-landing-page .avatar .avatar-svg {
      width: 100%; }
    body.is-landing-page .avatar #avatar-wheel.is-active {
      transform-origin: 84.25% 16%;
      animation: avatarWheel 10s linear infinite; }

@keyframes avatarWheel {
  0% {
    transform: rotate(0); }
  100% {
    transform: rotate(360deg); } }
    body.is-landing-page .avatar #avatar-circle.is-active {
      animation: avatarCircle 1s linear infinite alternate; }

@keyframes avatarCircle {
  0% {
    transform: translateY(0); }
  20% {
    transform: translateY(10%); }
  40% {
    transform: translateY(5%); }
  60% {
    transform: translateY(10%); }
  80% {
    transform: translateY(7%); }
  100% {
    transform: translateY(10%); } }
  body.is-landing-page .avatar-name {
    font-size: 1.5em;
    color: #39424e;
    font-weight: bold;
    padding: 10px 20px;
    width: 200px;
    margin: 20px auto 0 auto;
    background: #fff;
    text-align: center;
    border: 2px solid #39424e;
    position: relative;
    animation: avatarName 1s; }

@keyframes avatarName {
  0% {
    opacity: 0;
    left: 200px;
    text-indent: -800px; }
  100% {
    opacity: 1;
    left: 0;
    text-indent: 0; } }
  body.is-landing-page .avatar-title {
    font-size: 3em;
    font-weight: bold;
    color: #39424e;
    letter-spacing: 1px;
    white-space: nowrap;
    position: relative;
    animation: avatarTitle 1s; }
    @media (max-width: 479px) {
      body.is-landing-page .avatar-title {
        font-size: 2em;
        margin-top: 20px; } }

@keyframes avatarTitle {
  0% {
    transform: scaleX(2);
    opacity: 0; }
  100% {
    transform: scaleX(1);
    opacity: 1; } }
